import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';

import Dog from '../../components/dog';

const initialState = {};


@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;
		if (team === currentTeam) {
			dogStore.setCurrentTeam(undefined);
		} else {
			dogStore.setCurrentTeam(team);
		}
		if (util.isFunction(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, dogStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = dogStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col mb-2'>
							<div class={`border rounded rounded-pill px-4 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Dogs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadDogs();
    }

	loadDogs = () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		if (currentTeam) {
			dogStore.updateQueryFilter({ team: currentTeam });
		} else {
			dogStore.updateQueryFilter({});
		}
		dogStore.list(false, { addData: ['workoutSummary'] });
	}

	render() {
		const { dogStore } = this.props.stores;
		const { dogs } = dogStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 border-bottom'>
							<Teams stores={this.props.stores} callback={this.loadDogs} />
						</div>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>
							<h5>Hundene - {dogs.length} stk</h5>
						</div>
						{dogs && dogs.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-dog' />
									</div>
									<h5>
										Ingen hunder er registrert ennå.
									</h5>
								</div>
								Slik kommer du i gang:
								<ol>
									<li>Trykk på knappen "Legg til hunder" rett nedenfor.</li>
								</ol>
							</div>
						)}
						{dogs && dogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
							<a class='btn btn-primary mt-5 float-right'
								href='/dogs/edit/new'
							>
								<i class='fas fa-plus' /> Legg til hunder
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Dogs;
